.member {
  background-image: url("../Member/ad.png");
  background-size:100% auto;
  background-repeat: no-repeat;
  background-position: right top;
  position: relative;
}

.member:before {
  content: "";
  display: block;
  padding-top: 18%;
}

.msg {
  position: absolute;
  left: 0;
  top: 0;
  text-align: center;
}

.banner {
  text-align: center;
  margin-top: 4rem;
  top: 0;
  left: 0;
  position: absolute;
  margin-top: 14rem;
}

.memberDetail-ad{
  img{
    float: left;
    margin-top: 80px;
  }
  h2{
    font-size: 30px;
    color: #ffffff;
    float: left;
    margin-top: 80px;
    margin-left: 30px;
    margin-bottom: 0;
  }
  .memberDetail-ad-second-h2{
    font-size: 14px;
    color: #ffffff;
    text-align: left;
    display: inline-block;
    line-height: 20px;
    width: 1000px;
    margin-left: 150px;
    float: left;
    margin-top: -75px;
  }
}


.search {
  position: absolute;
  right: 0;
  margin: 1rem;
  margin-right: 2rem;
}

.row {
  border-radius: 10px;
  background: #fff;
  box-shadow: 3px 3px 7px rgba(0, 0, 0, 0.3);
  margin-top: 2rem;
  padding-bottom: 3rem;
}

.row ul {
  float: left;
}

.row ul li {
  float: left;
  margin-right: 1rem;
}

.title{
  width: 1200px;
  margin: 0 auto;
  margin-top: 120px;
  margin-bottom: 120px;
}

.proof-content-table{
  width: 890px;
  margin-top: 15px;
}

.proof-text-title{
  font-size: 18px;
  color: #333333;
}

.ele-layout{
  margin-top: 20px;
  margin-left: 20px;
  margin-right: 20px;
}

.proof-text-content{
  font-size: 14px;
  color: #333333;
  margin-top: 15px;
}

.text-td{
  width: 630px;
}

.text-td td{
  height: 80px;
  width: 630px;
  border-bottom: 1px solid #F6F6F6;
}

.upload-a{
  margin-top: 20px;
}

.file {
  position: relative;
  display: inline-block;
  background: transparent;
  border: 1px solid transparent;
  border-radius: 4px;
  padding: 4px 12px;
  overflow: hidden;
  color: transparent;
  text-decoration: none;
  text-indent: 0;
  line-height: 20px;
}
.file input {
  position: absolute;
  font-size: 212px;
  right: 0;
  top: 0;
  opacity: 0;
}
.file p {
  color: black;
  word-wrap: break-word;
  word-break:break-all;
}
.file:hover {
  background: transparent;
  border-color: transparent;
  color: transparent;
  text-decoration: none;
}

.file-area-td{
  width: 260px;
  height: 100%;
  //padding-left: 20px;
}



.proof-title{
  font-size: 30px;
  color: #333333;
  font-weight: lighter;
}

.short-code-input{
  display: block;
  width: 1090px;
  height: 50px;
  padding: 6px 12px;
  font-size: 14px;
  line-height: 1.42857143;
  color: #555;
  background-color: #fff;
  background-image: none;
  border: 1px solid #ccc;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
  -webkit-transition: border-color ease-in-out .15s, -webkit-box-shadow ease-in-out .15s;
  -o-transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
  transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
}

.short-code-input:focus {
  border-color: #66afe9;
  outline: 0;
  -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(102, 175, 233, .6);
  box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(102, 175, 233, .6);
}

.proof-btn{
  position: relative;
  margin-left: -1px;
  margin-right: -1px;
  width: 110px;
  height: 50px;
  border-bottom-right-radius: 5px;
  border-top-right-radius: 5px;
  background-image: url('../../containers/img/ic_search_normal.png');
  border: 0;
}

.short-code-input-group{
  margin-top: 30px;
}

.proof-btn:hover{
  background-image: url('../../containers/img/ic_search_hover.png');
}
.proof-btn:focus{
  z-index: 2;
  border: 0;
}
.proof-btn:active{
  z-index: 2;
  background-image: url('../../containers/img/ic_search_pressed.png');
}

.proof-upload-bg{
  margin-top: 13rem;
  text-align: center;
  margin-bottom: 13rem;
  margin-right: 6rem;
}


.butttonLeft {
  display: block;
  margin: 0 auto;
  margin-top: 2rem;
  margin-bottom: 2rem;

  outline: none;
  margin-top: 2rem;
  font-size: 2rem;
  border: 1px solid #0588fa;
  color: #0588fa;
  text-align: center;
  padding: 1% 5%;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  border-radius: 50px;
  background: #ffffff;
  -webkit-transition: all ease-in-out .3s;
  -moz-transition: all ease-in-out .3s;
  transition: all ease-in-out .3s;
}

.butttonLeft:hover {
  background: #0588fa;
