.left {
  border-radius: 10px;
  background: #fff;
  -webkit-box-shadow: 3px 3px 7px rgba(0,0,0,0.3);
  box-shadow: 3px 3px 7px rgba(0,0,0,0.3);
  margin-top: 3rem;
  padding-bottom: 3rem;

  height: 910px;
  width: 271px;
  padding-left: 0;
}

.left-div{
  background: #0185fb;
  width: 271px;
  img{
    margin-left: 50px;
  }
  h4{
    display: inline-block;
    color: #ffffff;
    margin-left: 22px;
  }
  h2{
    margin: 0;
    margin-top: 30px;
    position: absolute;
    top: 30%;
    left: 11%;
    font-size: 20px;
  }
}

.right {
  border-radius: 10px;
  background: #fff;
  -webkit-box-shadow: 3px 3px 7px rgba(0,0,0,0.3);
  box-shadow: 3px 3px 7px rgba(0,0,0,0.3);
  margin-top: 3rem;
  padding-bottom: 3rem;
  margin-right: -4rem;

  margin-left: 3rem;
  img {
    position: relative;
  }
}

.image {
  position: absolute;
  top: 35%;
  left: 0;
  img{
    display: block;
    margin: 0 auto;
    padding-left: 10%;
  }
  p {
    margin: 0 auto;
    display: block;
    text-align: center;
    margin-left: 10%;
    word-break:break-all;
  }
}

.butttonLeft {
  display: block;
  margin: 0 auto;
  margin-top: 2rem;
  margin-bottom: 2rem;
  margin-left: 41%;

  outline: none;
  margin-top: 2rem;
  font-size: 2rem;
  border: 1px solid #0588fa;
  color: #0588fa;
  text-align: center;
  padding: 1% 5%;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  border-radius: 50px;
  background: #ffffff;
  -webkit-transition: all ease-in-out .3s;
  -moz-transition: all ease-in-out .3s;
  transition: all ease-in-out .3s;
}

.butttonLeft:hover {
  background: #0588fa;
  color: #ffffff;
}

.my-ul {
  position: relative;
  left: 40%;
  top: 40%;
  ul{
    border-left: 1px solid #ddd;
    padding: 0;
    margin: 0;
    padding-left: 58px;
    position: absolute;
    width: 1200px;
    left: 20%;
  }
  li{
    text-indent: -.7em;
    line-height: 30px;
    display: block;
  }
  li::before {
    content: "\2022 ";
    color: white;
    font-size: 30px;
    position: relative;
    left: -42%;
  }
  img{
    padding-left: 11px;
  }
}

.book {
  h2 {
    position: absolute;
    top: 19%;
    left: 38%;
  }
  .bookP{
    position: absolute;
    top: 30%;
    left: 20%;
    p{
      width: 530px;
      word-wrap: break-word;
      word-break:break-all;
    }
    strong{
      font-size: 24px;
    }
    span{
      font-size: 16px;
      color: #3f4c6a;
    }
    hr{
      margin: 0px;
      height: 2px;
      border: 0px;
      background-color: #D5D5D5;
      color: #D5D5D5;
      opacity: 0.5;
      width: 520px;
      margin-left: 1px;
      margin-top: 40px;
      margin-bottom: 40px;
    }
  }
}

.my-projectDetail{
  h3{
    display: inline-block;
    padding-right: 30px;
    color: #3f4c6a;
    font-size: 18px;
    margin-bottom: 5px;
  }
  p {
    display: inline-block;
    color: #798399;
    font-size: 12px;
    word-wrap: break-word;
    word-break:break-all;
  }
}

.my-projectDetail-footer{
  margin-top: 40px;
  p{
    color: #fb7d30;
    font-size: 16px;
    width: 521px;
    line-height: 25px;
    word-wrap: break-word;
    word-break:break-all;
  }
  span{
    font-size: 14px;
    color: #cccccc;
    word-wrap: break-word;
    word-break:break-all;
  }
}


